import * as React from "react";
import { TagItem } from "../tags/tag";
import Tags from "../tags/tags";
import { _t } from "../../../utils/Translator";

export interface MailItem {
  subject: string;
  id: string;
}

export interface MailProps {
  mail: MailItem;
  tags: TagItem[];
}

export interface MailState {
  added: boolean;
  mail_subject: string;
}

export default class Mail extends React.Component<MailProps, MailState> {
  tagsRef;
  constructor(props, context) {
    super(props, context);
    this.state = {
      added: true,
      mail_subject: (props.mail && props.mail.subject) || "",
    };
    this.tagsRef = React.createRef();
  }

  componentDidUpdate(prevProps) {
    // Ensure prevProps.mail and this.props.mail are defined before accessing their properties
    if (prevProps.mail && this.props.mail && prevProps.mail.id !== this.props.mail.id) {
      this.setState({
        mail_subject: this.props.mail.subject || "",
      });
    }
  }
 
  resetMail = () => {
    this.setState({
      added: true,
      mail_subject: (this.props.mail && this.props.mail.subject) || "",
    });
    if (this.tagsRef.current) {
      this.tagsRef.current.resetTags();
    }
  };

  // Helper function to get the email recipients 
  extractEmailAddresses = (recipients: any) => {
    // No recipients
    if (!recipients) {
        return []; 
    }   
    // Multiple recipients
    if (Array.isArray(recipients)) {
        return recipients.map((recipient) => recipient.emailAddress);
    }   
    // Single recipient
    return [recipients.emailAddress];
  };  

  // Helper function to get the body of the email
  getEmailBody = (format: Office.CoercionType): Promise<string> => {
    return new Promise((resolve, reject) => {
        const item = Office.context.mailbox.item;

        if (!item) {
            reject("No email item found.");
            return;
        }

        item.body.getAsync(format, (asyncResult) => {
            if (asyncResult.status === Office.AsyncResultStatus.Succeeded) {
                resolve(asyncResult.value);
            } else {
                reject(asyncResult.error.message);
            }
        });
    });
  };

  getAttachmentData = async () => {
    const attachments = Office.context.mailbox.item.attachments;
  
    // Create promises to fetch each attachment's content
    const attachmentPromises = attachments.map((attachment) => {
      return new Promise((resolve, reject) => {
        Office.context.mailbox.item.getAttachmentContentAsync(attachment.id, (asyncResult) => {
          if (asyncResult.status === Office.AsyncResultStatus.Succeeded) {
            resolve({
              filename: attachment.name, // Attachment name
              content: asyncResult.value.content, // Base64 content
            });
          } else {
            reject(new Error(`Failed to retrieve attachment content for ${attachment.name}: ${asyncResult.error.message}`));
          }
        });
      });
    });
  
    // Wait for all promises to resolve and return the result
    return await Promise.all(attachmentPromises);
  };
  
  getMailData = async () => {
    if (!this.state.added) {
      return {};
    }
    const item = Office.context.mailbox.item;
    const [htmlBody, plainTextBody] = await Promise.all([
      this.getEmailBody(Office.CoercionType.Html),
      this.getEmailBody(Office.CoercionType.Text),
    ]);
    const attachments = await this.getAttachmentData();
    return {
      mail: this.props.mail,
      from: item.from.emailAddress,
      to: this.extractEmailAddresses(item.to),
      cc: this.extractEmailAddresses(item.cc),
      bcc: this.extractEmailAddresses(item.bcc),
      timeCreated: item.dateTimeCreated.toUTCString(),
      htmlBody: htmlBody,
      plainTextBody: plainTextBody,
      subject: this.state.mail_subject,
      tags: this.tagsRef.current.getTagsData(),
      attachments: attachments,
    };
  };

  add = () => {
    this.setState({ added: true, mail_subject: (this.props.mail && this.props.mail.subject) || "" });
  };

  remove = () => {
    this.setState({ added: false });
  };

  render() {
    if (this.state.added) {
      return (
        <div>
          <div className="page-section-title">{_t("Email")}</div>
          <div className="card">
            <div className="card-body">
              <div className="input-group mt-3">
                <input
                  className="form-control"
                  type="text"
                  value={this.state.mail_subject}
                  onChange={(e) => this.setState({ mail_subject: e.target.value })}
                />
                <button
                  className="btn btn-primary"
                  type="button"
                  aria-label={_t("Close")}
                  onClick={this.remove}
                >
                  <i className="fa fa-trash" aria-hidden="true" />
                </button>
              </div>
              <div className="mt-2">
                <Tags ref={this.tagsRef} tags={this.props.tags}></Tags>
              </div>
            </div>
          </div>
        </div>
      );
    } else {
      return (
        <>
          <div className="page-section-title">{_t("Email")}</div>
          <div className="mt-3">
            <button className="btn btn-outline-primary btn-sm" type="button" onClick={this.add}>
              <i className="fa fa-plus-circle" aria-hidden="true"></i>&nbsp;{_t("Add e-mail")}
            </button>
          </div>
        </>
      );
    }
  }
}
